@import "../src/common.css";
* {
  margin: 0;
  padding: 0;
}
body {
  font: 0.28rem "Century Gothic", Futura, sans-serif;
  margin: 0.4em;
}
ol,
ul {
  padding-left: 0.6rem;
}
.board-row:after {
  clear: both;
  content: "";
  display: table;
}
.status {
  margin-bottom: 0.2rem;
}
.square {
  background: #fff;
  border: 0.02rem solid #999;
  float: left;
  font-size: 0.48rem;
  font-weight: bold;
  line-height: 0.68rem;
  height: 0.68rem;
  margin-right: -0.02rem;
  margin-top: -0.02rem;
  padding: 0;
  text-align: center;
  width: 0.68rem;
}
.square:focus {
  outline: none;
}
.kbd-navigation .square:focus {
  background: #ddd;
}
.game {
  display: flex;
  flex-direction: row;
}
.game-info {
  margin-left: 0.4rem;
}
.App {
  width: 100vw;
  height: 100vh;
  background: #fff;
  margin-top: 0.6rem;
  color: #212121;
  font-family: "Open Sans", sans-serif;
  display: grid;
  place-items: center;
}
body {
  margin: 0%;
  padding: 0%;
}
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 300;
  font-stretch: normal;
  src: url(https://fonts.gstatic.com/s/opensans/v27/memSYaGs126MiZpBA-UvWbX2vVnXBbObj2OVZyOOSr4dVJWUgsiH0B4gaVc.ttf) format('truetype');
}
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  font-stretch: normal;
  src: url(https://fonts.gstatic.com/s/opensans/v27/memSYaGs126MiZpBA-UvWbX2vVnXBbObj2OVZyOOSr4dVJWUgsjZ0B4gaVc.ttf) format('truetype');
}
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 700;
  font-stretch: normal;
  src: url(https://fonts.gstatic.com/s/opensans/v27/memSYaGs126MiZpBA-UvWbX2vVnXBbObj2OVZyOOSr4dVJWUgsg-1x4gaVc.ttf) format('truetype');
}
.joinChatContainer {
  display: flex;
  flex-direction: column;
  text-align: center;
}
.joinChatContainer h3 {
  font-size: 0.8rem;
  margin-bottom: 0.32rem;
}
.joinChatContainer input {
  width: 4.2rem;
  height: 0.8rem;
  margin: 0.14rem;
  border: 0.04rem solid #43a047;
  border-radius: 0.1rem;
  padding: 0.1rem;
  font-size: 0.32rem;
}
.joinChatContainer button {
  width: 4.5rem;
  height: 1rem;
  margin: 0.14rem;
  border: none;
  border-radius: 0.1rem;
  padding: 0.1rem;
  font-size: 0.32rem;
  background: #43a047;
  color: #fff;
  cursor: pointer;
}
.joinChatContainer button:hover {
  background: #2e7d32;
}
.chat-window {
  width: 6rem;
  height: 8.4rem;
  margin-left: 1rem;
}
.chat-window p {
  margin: 0;
}
.chat-window .chat-header {
  height: 0.9rem;
  border-radius: 0.12rem;
  background: #263238;
  position: relative;
  cursor: pointer;
}
.chat-window .chat-header p {
  display: block;
  padding: 0 1em 0 2em;
  color: #fff;
  font-weight: 700;
  line-height: 0.9rem;
}
.chat-window .chat-body {
  height: 8rem;
  border: 1px solid #263238;
  background: #fff;
  position: relative;
}
.chat-window .chat-body .message-container {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
}
.chat-window .chat-body .message-container::-webkit-scrollbar {
  display: none;
}
.chat-window .chat-body .message {
  height: auto;
  padding: 0.2rem;
  display: flex;
}
.chat-window .chat-body .message .message-content {
  width: auto;
  height: auto;
  min-height: 0.8rem;
  max-width: 2.4rem;
  background-color: #43a047;
  border-radius: 0.1rem;
  color: white;
  display: flex;
  align-items: center;
  margin-right: 0.1rem;
  margin-left: 0.1rem;
  padding-right: 0.1rem;
  padding-left: 0.1rem;
  overflow-wrap: break-word;
  word-break: break-word;
}
#other {
  justify-content: flex-start;
}
#other .message-content {
  justify-content: flex-start;
  background-color: cornflowerblue;
}
#other .message-meta {
  justify-content: flex-start;
  margin-left: 0.1rem;
}
#you {
  justify-content: flex-end;
}
#you .message-content {
  justify-content: flex-end;
  background-color: #43a047;
}
#you .message-meta {
  justify-content: flex-end;
  margin-right: 0.1rem;
}
.message-meta #author {
  margin-left: 0.1rem;
  font-weight: bold;
}
.chat-window .chat-body .message .message-meta {
  display: flex;
  font-size: 0.24rem;
}
.chat-window .chat-footer {
  height: 0.8rem;
  border: 1px solid #263238;
  border-top: none;
  display: flex;
}
.chat-window .chat-footer input {
  height: 100%;
  flex: 85%;
  border: 0;
  padding: 0 0.7em;
  font-size: 1em;
  border-right: 1px dotted #607d8b;
  outline: none;
  font-family: "Open Sans", sans-serif;
}
.chat-window .chat-footer button {
  border: 0;
  display: grid;
  place-items: center;
  cursor: pointer;
  flex: 15%;
  height: 100%;
  background: transparent;
  outline: none;
  font-size: 0.5rem;
  color: lightgray;
}
.chat-window .chat-footer button:hover {
  color: #43a047;
}
.hide {
  opacity: 0 !important;
}
